import React,{useState,useEffect} from 'react';
import About from './About';
import Language from './Language';

const Header = (props)=>{
  const [name,setName] = useState('');
  const [head,setHead] = useState([]);
  const [view,setView] = useState(true);
  const [sign,setSign] = useState('');
  function viewHead(){
    (view)?setView(false):setView(true);
  }
 
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let items = [];
      entry.forEach(e=>{
        let index = 'gsx$'+'head'
        let item = e[index]['$t'];
        if(item.length>0) items.push(item);
      });
      setName(items[props.lang]);
      setHead(items); 
      document.title=name;
    });
  });
  return(
    <>
    <div className="header f">
    <b>
     <br/>
	   <h1>{name}</h1>
     <br/>
      {view&&<div id="subhead">
      <div>
        <About menu={head} lang={props.lang} handlePage={props.handlePage}/>
        <Language title={head} lang={props.lang} handleLang={props.handleLang}/>
      </div>
      <br/>
      <div>
        <a href="#links" onClick={()=>{props.handlePage('links')}}>{head[21+props.lang]}</a>
        <a href={head[36+props.lang]}>{head[33+props.lang]}</a>
        <a href={head[27+props.lang]}>{head[24+props.lang]}</a>
      </div>
      <br/> 
       
      </div>
      }
      <div id="x">
        <a href="#x" onClick={()=>{viewHead()}}>{(view)?<span>&#10006;</span>:<span>☰</span>}</a>  
      </div>
      </b>
      </div>
    </>
  )
}

export default Header;