import React,{useState} from 'react';

const About = (props)=>{
  const [about,setAbout] = useState('me');
  function selectAbout(x){
    setAbout(x);
    props.handlePage(x);
  }
  return(
    <>
    <div id="about" class="dropdown">
      <b>
      <a href="#about">{props.menu[39+props.lang]}</a>  
      <br/>
      <button class="dropbtn"><a href="#selectAbout">{props.menu[42+props.lang]}</a></button>
      <div class="dropdown-content">
         <a href="#bio" onClick={()=>{selectAbout('bio')}}>{props.menu[3+props.lang]}</a>
          <a href="#education" onClick={()=>{selectAbout('education')}}>{props.menu[6+props.lang]}</a>
          <a href="#certificates" onClick={()=>{selectAbout('certificates')}}>{props.menu[9+props.lang]}</a> 
          <a href="#experience" onClick={()=>{selectAbout('experience')}}>{props.menu[12+props.lang]}</a>
          <a href="#projects" onClick={()=>{selectAbout('projects')}}>{props.menu[15+props.lang]}</a>
          <a href="#skills" onClick={()=>{selectAbout('skills')}}>{props.menu[18+props.lang]}</a>
          <a href="#fun" onClick={()=>{selectAbout('fun')}}>{props.menu[30+props.lang]}</a>
      </div>
      </b>
    </div>
    </>
  )
}

export default About;