import React,{useState} from 'react';

const Language = (props)=>{
  return(  
    <>
    <div id="language" class="dropdown">
      <b>
      <a href="#language">{props.title[45+props.lang]}</a>  
      <br/>
      <button class="dropbtn">
        {(props.lang==0)&&<a>english</a>}
        {(props.lang==1)&&<a>日本語</a>}
        {(props.lang==2)&&<a>中文</a>}
      </button>
      <div class="dropdown-content">
        {(props.lang!=0)&&<a onClick={()=>{props.handleLang(0)}}>english</a>}
        {(props.lang!=1)&&<a onClick={()=>{props.handleLang(1)}}>日本語</a>}
        {(props.lang!=2)&&<a onClick={()=>{props.handleLang(2)}}>中文</a>}
      </div>
      </b>
    </div>
    </>
  )
}

export default Language;